@import url("https://fonts.googleapis.com/css?family=Poppins:400,500,600,900|Roboto:300i,400,400i,500&display=swap");
.newsCard {
  background: #FFFFFF;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.05);
  border-radius: 2px;
  transition: 0.2s; }
  .newsCard-img {
    overflow: hidden;
    display: block;
    cursor: pointer;
    height: 192px; }
    .newsCard-img img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: 0.2s; }
  .newsCard-content {
    padding: 20px 15px 30px; }
  .newsCard-date {
    color: #F5842D;
    padding-bottom: 15px; }
  .newsCard-title {
    padding-bottom: 25px;
    min-height: 100px; }
  .newsCard-link {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    line-height: 1;
    transition: 0.2s; }
    .newsCard-link svg {
      margin-left: 10px;
      transition: 0.2s; }
    .newsCard-link:hover {
      color: #F5842D; }
      .newsCard-link:hover svg {
        transform: translateX(15px); }
  .newsCard:hover {
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1); }
    .newsCard:hover .newsCard-img img {
      transform: scale(1.05); }
    .newsCard:hover .newsCard-link {
      transform: translateX(5px); }
