@import url("https://fonts.googleapis.com/css?family=Poppins:400,500,600,900|Roboto:300i,400,400i,500&display=swap");
.gameCard {
  display: block;
  text-decoration: none;
  color: #333333;
  transition: 0.2s;
  box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.1); }
  .gameCard:hover {
    text-decoration: none;
    cursor: pointer; }
    .gameCard:hover .gameCard-img > img {
      transform: scale(1.05); }
    .gameCard:hover .gameCard-overlay {
      display: flex; }
  .gameCard-img {
    overflow: hidden;
    position: relative; }
    .gameCard-img > img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: 0.2s; }
  .gameCard-overlay {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    justify-content: center;
    flex-direction: column;
    align-items: center;
    transition: 0.2s; }
  .gameCard-content {
    background-color: #ffffff;
    padding: 20px; }
  .gameCard-title {
    padding-bottom: 25px;
    line-height: 20px; }
  .gameCard-meta {
    position: absolute;
    bottom: 20px;
    text-align: center;
    width: 100%; }
  .gameCard-name {
    font-weight: 500;
    font-size: 18px;
    line-height: 26px;
    color: #ffffff; }
  .gameCard-icon {
    display: inline-block;
    margin-bottom: 10px;
    width: 40px;
    height: 40px; }
  .gameCard-platforms {
    margin: 25px 0 60px; }
    .gameCard-platforms a {
      margin: 0 5px;
      display: inline-block;
      transition: 0.2s; }
      .gameCard-platforms a img {
        width: 120px; }
      .gameCard-platforms a:hover {
        transform: scale(1.05); }

@media only screen and (max-width: 1259px) {
  .gameCard-title {
    padding-bottom: 15px; }
  .gameCard-overlay {
    display: flex; } }
