@import url("https://fonts.googleapis.com/css?family=Poppins:400,500,600,900|Roboto:300i,400,400i,500&display=swap");
.socialBlock {
  background: rgba(238, 238, 238, 0.5);
  padding: 0 45px;
  height: 100%;
  min-height: 388px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center; }
  .socialBlock-list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%; }
    .socialBlock-list li a {
      border: 6px solid #ffffff;
      display: block;
      border-radius: 50%;
      transition: 0.2s; }
      .socialBlock-list li a:hover {
        transform: scale(1.1); }
  .socialBlock-title {
    color: #15495D;
    padding-bottom: 30px; }
  .socialBlock-subtitle {
    padding-bottom: 30px; }
  .socialBlock--company {
    height: 330px;
    min-height: 0; }

@media only screen and (max-width: 1259px) {
  .socialBlock {
    padding: 0 25px; } }

@media only screen and (max-width: 991px) {
  .socialBlock {
    margin-top: 30px;
    min-height: 0;
    padding: 50px 30px;
    height: auto; }
    .socialBlock-list {
      justify-content: center; }
      .socialBlock-list li {
        margin: 0 10px; } }
